    sun: #F2B900
   */
@import url('https://fonts.googleapis.com/css?family=Lato');
@import url('../static/css/open-weather-icons.css');
#app {
    font-family: 'Lato', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
}
body {
    margin: 0;
    padding: 0;
    background-color: #FFFAF4;
}
h1, h2 {
    font-weight: normal;
}
ul {
    list-style-type: none;
    padding: 0;
}
header {
    width: 400px;
    padding: 1em;
    position: relative;
}
header input {
    width: 100%;
    height: 20px;
    text-indent: 5px;
}
#results {
    width: 93%;
    position: absolute;
    z-index: 10;
}
#results ul {
    text-align: left;
    margin: 0;
}
#results li {
    padding: 5px;
    background-color: #FCF2E3;
}
#results li:hover {
    background-color: #F7E8D7;
    cursor: pointer;
}
.container {
    padding: 1em;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
}
.weather-card {
    width: 170px;
    height: 270px;
    padding: 1em;
    background-color: #F2B900;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    position: relative;
    overflow: hidden;
    cursor: pointer;
}
.weather-card-modal {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 20;
    width: 100%;
    height: 100%;
    background: rgba(12,201,43,0.6);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: #FFF;
    -webkit-transition: top .3s;
    transition: top .3s;
}
.delete {
    background: rgba(255,12,49,0.6);
}
.weather-card:hover > .weather-card-modal {
    top: 0;
}
.card-header h1 {
    font-size: 1.2em;
}
.card-body p {
    text-transform: uppercase;
    font-size: .7em;
}
.card-body i {
    font-size: 3em;
}
.card-footer h1 {
    font-size: 1.8em;
}
.weather-board {
    width: 100%;
    max-width: 800px;
    padding: 1em;
}
.weather-board ul {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
}

/* result transition */
.result-enter-active,
  .result-leave-active {
    -webkit-transition: opacity .7s, -webkit-transform .7s;
    transition: opacity .7s, -webkit-transform .7s;
    transition: opacity .7s, transform .7s;
    transition: opacity .7s, transform .7s, -webkit-transform .7s;
}
.result-enter,
  .result-leave-to {
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}

/* weatherboard transition */
.list-ul {
    -webkit-transition: all .7s;
    transition: all .7s;
}
.list-enter-active,
  .list-leave-active {
    -webkit-transition: opacity .7s, -webkit-transform .7s;
    transition: opacity .7s, -webkit-transform .7s;
    transition: opacity .7s, transform .7s;
    transition: opacity .7s, transform .7s, -webkit-transform .7s;
}
.list-enter,
  .list-leave-to {
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}




